    const open = () => {
      this.selectedEvent = events.event;
      this.selectedElement = events.nativeEvent.target;
      setTimeout(() => this.selectedOpen = true, 10)
    }
    if (this.selectedOpen) {
      this.selectedOpen = false
      setTimeout(open, 10)
    } else {
      open()
    }
    events.nativeEvent.stopPropagation()
  }

