  rnd (a: number, b: number) {
    return Math.floor((b - a + 1) * Math.random()) + a
  }

  saveSchedule(){
    this.dialog = false;
    console.log(this.time);
    console.log(this.date);
    console.log(this.category);
    // let formData = new FormData();
